import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { URL_PATHS } from '@shared';

const routes: Routes = [

  {
    path: '',
    children: [
      {
        path: URL_PATHS.LIST_APPLICATION,
        loadChildren: () => import('./list-s2-application/list-s2-application.module')
          .then(m => m.ListS2ApplicationModule)
      },
      {
        path: URL_PATHS.LIST_SPECIAL_APPLICATION,
        loadChildren: () => import('./list-s2-application/list-s2-application.module')
          .then(m => m.ListS2ApplicationModule)
      },
      {
        path: URL_PATHS.ADD_APPLICATION,
        loadChildren: () => import('./add-s2-application/add-s2-application.module')
          .then(m => m.AddS2ApplicationModule)
      },
      {
        path: URL_PATHS.UPDATE_APPLICATION + '/:id',
        loadChildren: () => import('./add-s2-application/add-s2-application.module')
          .then(m => m.AddS2ApplicationModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class S2ApplicationRoutingModule { }
